import React from 'react';
import CSSTransitionGroup from 'react-addons-css-transition-group';
import {render} from 'react-dom';
import styles from './index.css';
import polyfills from './polyfills/es2015';
import Course from './components/Course';
import { Router, Route, IndexRoute, IndexRedirect, Link } from 'react-router';

// Setup the react addons, and attach a handle to the React
// class to the window.
React.addons = {};
React.addons.CSSTransitionGroup = CSSTransitionGroup;
window.React = React;

render(
  <Router>
    <Route path="/" component={Course} />
  </Router>,
  document.getElementById('course')
);



