import Store from './Store';
import Dispatcher from '../Dispatcher';
import CourseConstants from '../constants/CourseConstants';
import CourseActions from '../actions/CourseActions';
import AjaxUtility from '../../utilities/AjaxUtility';

class CourseStore extends Store {
  constructor () {
    super();
    this.config;
    this.messages = [];
    this.loading = [];
    this.project;
    this.savedData;
  }
  getConfig () {
    return this.config;
  }
  getLoading () {
    return this.loading;
  }
  getMessages () {
    return this.messages;
  }
  getProject () {
    return this.project;
  }
  getSavedData () {
    return this.savedData;
  }
  handleAction (action) {
    switch (action.type) {
      case CourseConstants.ADD_LOADING: {
        this.loading.push(action.data);
        break;
      }
      case CourseConstants.ADD_MESSAGE: {
        this.messages.push(action.data);
        break;
      }
      case CourseConstants.CLEAR_LOADING: {
        this.handleClearLoading(action.data);
        break;
      }
      case CourseConstants.CLEAR_MESSAGE: {
        this.handleClearMessage(action.data);
        break;
      }
      case CourseConstants.GET_DATA: {
        this.handleGetData();
        break;
      }
      case CourseConstants.LOAD_COURSE: {
        this.handleLoadCourse();
        break;
      }
      case CourseConstants.SAVE_DATA: {
        if (!this.savedData) {
          this.savedData = {};
        }

        let data = action.data;
        this.savedData[data.name] = data.value;

        if (this.project) {
          if (typeof Storage !== 'undefined') {
            localStorage.setItem(this.project.name.replace(/\s/g, '-'), JSON.stringify(this.savedData));
          } else {
            document.cookie = JSON.stringify(this.savedData);
          }
        }

        break;
      }
    }
    this.emitChange();
  }
  handleClearLoading (name) {
    for (let i = 0; i < this.loading.length; i++) {
      if (this.loading[i] == name) {
        // Don't break the loop after we clear out the
        // specified loading item, just in case there
        // is more than one in here...
        this.loading.splice(i, 1);
      }
    }
    this.emitChange();
  }
  handleClearMessage (index) {
    if (index >= 0 && index < this.messages.length) {
      this.messages.splice(index, 1);
    }
  }
  handleGetData () {
    var data;
    if (this.project) {
      if (typeof Storage !== 'undefined') {
        data = localStorage.getItem(this.project.name.replace(/\s/g, '-'));
        if (data) {
          data = JSON.parse(data);
        }
      } else {
        try {
          data = JSON.parse(document.cookie);
        } catch (err) {}
      }
    }
    this.savedData = data;
    this.emitChange();
  }
  handleLoadCourse () {
    this.loading = this.loading.concat(['config', 'project']);

    const configClearLoading = () => {
      this.handleClearLoading('config');
    };
    const configError = (err) => {
      configClearLoading();
      this.messages.push({
        message: `Error loading config.json file - ${err}`,
        type: 'danger'
      });
      this.emitChange();
    };
    const configSuccess = (data) => {
      configClearLoading();
      this.config = data;
      this.emitChange();
    };
    const projectClearLoading = () => {
      this.handleClearLoading('project');
    };
    const projectError = (err) => {
      projectClearLoading();
      this.messages.push({
        message: `Error loading project.json file - ${err}`,
        type: 'danger'
      });
      this.emitChange();
    };
    const projectSuccess = (data) => {
      projectClearLoading();
      this.project = data;
      this.handleGetData();
    };

    AjaxUtility.loadFile({
      dataType: 'json',
      forceFresh: true,
      error: projectError,
      success: projectSuccess,
      url: './project.json'
    });
    AjaxUtility.loadFile({
      dataType: 'json',
      forceFresh: true,
      error: configError,
      success: configSuccess,
      url: './config.json'
    });
  }
}

const instance = new CourseStore();
instance.dispatchToken = Dispatcher.register(instance.handleAction.bind(instance));

export default instance;



