var WindowUtility = {};
WindowUtility.urlParams;
WindowUtility.getUrlParams = function () {
  if (this.urlParams) {
    return this.urlParams;
  }

  var urlParams = {};
  var search = window.location.search.replace(/\?/g, '');
  var params = search.split('&');
  for (let i = 0; i < params.length; i++) {
    let param = params[i];
    let paramPair = param.split('=');
    urlParams[paramPair[0]] = paramPair[1];
  }

  this.urlParams = urlParams;
}.bind(WindowUtility);
export default WindowUtility;



