class AjaxUtility {
  constructor () {}
  static loadFile (details) {
    const xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = () => {
      if (xhttp.readyState === XMLHttpRequest.DONE) {
        if (xhttp.response) {
          let response = xhttp.response;
          switch (details.dataType) {
            case 'json': {
              try {
                response = JSON.parse(response);
              } catch(err) {
                details.error(`Error: unable to parse JSON data - ${response}`);
              }
            }
          }
          details.success(response);
        } else {
          details.error(`Error from server: ${xhttp.statusText} --> ${xhttp.response}`);
        }
      }
    };
    xhttp.open('GET', (
      details.forceFresh
        ? (details.url + '?timestamp=' + Date.now())
        : (details.url)
    ), true);
    xhttp.send();
  }
}

export default AjaxUtility;



