import CourseStore from '../../flux/stores/CourseStore';

export default {
  findNode (guid, parent) {
    const find = (guid, parent) => {
      parent = parent || CourseStore.getProject();
      if (parent) {
        if (parent.guid == guid) {
          return parent;
        } else if (parent.nodes) {
          for (let i = 0; i < parent.nodes.length; i++) {
            let node = find(guid, parent.nodes[i]);
            if (node) {
              return node;
            }
          }
        }
      }
      return null;
    };
    return find(guid, parent);
  }
}



