import React, {Component} from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import styles from './Course.css';
import UI from '../UI';
import CourseStore from '../../flux/stores/CourseStore';
import CourseActions from '../../flux/actions/CourseActions';
import CourseConstants from '../../flux/constants/CourseConstants';
import AjaxUtility from '../../utilities/AjaxUtility';
import NodeUtility from '../../utilities/NodeUtility';
import WindowUtility from '../../utilities/WindowUtility';

// Setup handles to the actions and constants
// from the window so they can be accessed globally.
window.CourseActions = CourseActions;
window.CourseConstants = CourseConstants;
window.AjaxUtility = AjaxUtility;
window.NodeUtility = NodeUtility;
window.WindowUtility = WindowUtility;

class Course extends Component {
  componentDidMount () {
    this.handleCourseStoreChange = this.onCourseStoreChange.bind(this);
    CourseStore.addChangeListener(this.handleCourseStoreChange);
    CourseActions.loadCourse();
  }
  componentWillUnmount () {
    CourseStore.removeChangeListener(this.handleAction);
  }
  constructor () {
    super();
    this.state = this.getState();
  }
  getState () {
    return {
      config: CourseStore.getConfig(),
      messages: CourseStore.getMessages(),
      loading: CourseStore.getLoading(),
      project: CourseStore.getProject(),
      savedData: CourseStore.getSavedData()
    };
  }
  onCourseStoreChange () {
    this.setState(this.getState());
  }
  render () {
    var messages = this.state.messages.map(this.renderMessages);
    var preloader;
    if (this.state.loading && this.state.loading.length) {
      preloader = (
        <div className="preloader sudo-vert-align-cont">
          <p className="sudo-vert-align-child">Loading...{this.state.loading[0]}</p>
        </div>
      );
    }

    return (
      <div className="Course">
        <UI {...this.state} />
        <div className="messages-area">
          {messages}
        </div>
        <ReactCSSTransitionGroup
          transitionEnterTimeout={500}
          transitionName="fade">
            {preloader}
        </ReactCSSTransitionGroup>
      </div>
    );
  }
  renderMessages (message, index) {
    return (
      <div className="message" key={index}>
        <div className={`alert alert-${message.type}`}>
          <a
            className="close"
            href="javascript:void(0);"
            onClick={CourseActions.clearMessage.bind(this, index)}>
              &times;
          </a>
          {message.message}
        </div>
      </div>
    );
  }
}

export default Course;



