import React, {Component} from 'react';
import styles from './ComponentLoader.css';
import AjaxUtility from '../../../utilities/AjaxUtility';
import NodeUtility from '../../../utilities/NodeUtility';
import WindowUtility from '../../../utilities/WindowUtility';
import CourseActions from '../../../flux/actions/CourseActions';
import CourseConstants from '../../../flux/constants/CourseConstants';

class ComponentLoader extends Component {
  componentDidMount () {
    this.load();
  }
  componentWillUnmount () {
    this.unmounting = true;
  }
  constructor () {
    super();
    this.data;
    this.state = { loaded: false, template: undefined };
    this.Template;
    this.unmounting = false;
  }
  load () {
    this.toggleLoading(true);
    if (
      this.props.config &&
      this.props.config.testing &&
      this.props.config.testing.debug
    ) {
      console.log('ComponentLoader.load() => ');
      console.log('this == ');
      console.log(this);
    }
    const error = (err) => {
      this.toggleLoading(false);
    };
    const success = (data) => {
      if (!this.unmounting) {
        this.setState({ template: data });
      }
      this.toggleLoading(false);
    };
    var node;
    var template;
    try {
      node = NodeUtility.findNode(this.props.guid);
      template = (
        this.props.template
          ? this.props.template
          : this.props.guid
            ? node.template
            : undefined
      );
    } catch (err) {
      CourseActions.addMessage({
        message: `ComponentLoader.load() => error getting template - ${err}`,
        type: 'danger'
      });
      this.toggleLoading(false);
    }
    if (node && node.data && node.data != '') {
      this.data = JSON.parse(node.data);
    }
    if (template) {
      AjaxUtility.loadFile({
        dataType: 'json',
        forceFresh: true,
        error,
        success,
        url: `./templates/${template}.json`
      });
    } else {
      CourseActions.addMessage({
        message: `ComponentLoader.load() => no template defined.`,
        type: 'danger'
      });
    }
  }
  render () {
    if (
      this.props.config &&
      this.props.config.testing &&
      this.props.config.testing.debug
    ) {
      console.log('ComponentLoader.render() => ');
      console.log('this.props == ');
      console.log(this.props);
      console.log('this.state == ');
      console.log(this.state);
    }
    var state = this.state;
    var className = 'component-loader-wrapper';
    if (!state.template) {
      if (state.loaded) {
        setTimeout(() => {
          CourseActions.addMessage({
            message: `ComponentLoader.render() => unable to load template data for '${this.props.template}'`,
            type: 'danger'
          });
        }, 0);
      }
      return <div className={className} />;
    }
    var templateData = state.template;
    var reactTemplate = (
      templateData
        ? (templateData.data.react.processed || templateData.data.react.value || null)
        : null
    );
    var templateName = (
      this.props.name
        ? this.props.name.replace(/\s/g, '')
        : ''
    );
    var Template = this.Template;
    if (!Template && reactTemplate) {
      try {
        Template = eval(`(function () { ${reactTemplate} return Template; })();`);
        this.Template = Template;
      } catch (err) {
        setTimeout(() => {
          CourseActions.addMessage({
            message: `ComponentLoader.render() => unable to evaluate template '${templateData.name}' - ${err}`,
            type: 'danger'
          });
        }, 0);
        Template = {};
        return <div className={className} />;
      }
    }
    className += ` ${templateData.name}`;
    if (this.props.className) {
      className += ` ${this.props.className}`;
    }
    if (!this.data) {
      var templateProps = (
        this.props.data
          ? this.props.data
          : NodeUtility.findNode(this.props.guid)
            ? NodeUtility.findNode(this.props.guid).data
            : null
      );
      if (templateProps && typeof templateProps == 'string') {
        try {
          templateProps = JSON.parse(templateProps);
          this.data = templateProps;
        } catch (err) {
          this.data = {};
          setTimeout(() => {
            CourseActions.addMessage({
              message: `ComponentLoader.render() => unable to parse template properties - ${err}`,
              type: 'danger'
            });
          }, 0);
        }
      }
    }
    if (
      this.props.config &&
      this.props.config.testing &&
      this.props.config.testing.debug
    ) {
      console.log('this.data == ');
      console.log(this.data);
    }
    return (
      <div className={className}>
        <style
          rel="stylesheet"
          type="text/css"
          dangerouslySetInnerHTML={{ __html: (templateData.data.css.processed || templateData.data.css.value) }}>
        </style>
        <Template
          {...this.props}
          {...this.data} />
      </div>
    );
  }
  toggleLoading (add) {
    if (
      this.props.config &&
      this.props.config.ui &&
      this.props.config.ui.persistentPreloader
    ) {
      CourseActions[(
        add
          ? 'addLoading'
          : 'clearLoading'
      )](this.props.guid);
    }
    if (!this.unmounting) {
      this.setState({ loaded: !add });
    }
  }
}

export default ComponentLoader;



