import Dispatcher from '../Dispatcher';
import CourseConstants from '../constants/CourseConstants';

export default {
  addLoading (data) {
    Dispatcher.dispatch({
      data,
      type: CourseConstants.ADD_LOADING
    });
  },
  addMessage (data) {
    Dispatcher.dispatch({
      data,
      type: CourseConstants.ADD_MESSAGE
    });
  },
  clearLoading (data) {
    Dispatcher.dispatch({
      data,
      type: CourseConstants.CLEAR_LOADING
    });
  },
  clearMessage (data) {
    Dispatcher.dispatch({
      data,
      type: CourseConstants.CLEAR_MESSAGE
    });
  },
  getData () {
    Dispatcher.dispatch({
      type: CourseActions.GET_DATA
    });
  },
  loadCourse () {
    Dispatcher.dispatch({
      type: CourseConstants.LOAD_COURSE
    });
  },
  saveData (data) {
    Dispatcher.dispatch({
      data,
      type: CourseConstants.SAVE_DATA
    });
  }
};



